import React, { useState } from 'react'

import { renderToStaticMarkup } from 'react-dom/server';

import './Nand.css'

const Nand = ({ style, divWrap, pos={ top: 0, left: 0 }, id, rot, onDoubleClick, onMouseDown, onMouseUp }) => {

    console.log('in Nand id = ', id, ' rot = ' + rot)

    const [hovering, setHovering] = useState(false)
    const [draggable, setDraggable] = useState(false)


    const pathStyle  = {
        fill: style.fill, 
        fillOpacity: '0.75', 
        fillRule: 'evenodd', 
        stroke: '#000000', 
        strokeWidth: '12', 
        strokeLinecap: 'square', 
        strokeLinejoin: 'miter', 
        strokeOpacity: '1', 
        strokeMiterlimit: '4', 
        strokeDasharray: 'none'
    }

    const styleAndPosition = {
        ...style,
        top: pos.top,
        left: pos.left,
        transform: rot,
    }

    
    const SVG = ({ svgStyle, small }) => (
        <svg style={svgStyle} xmlns="http://www.w3.org/2000/svg" mlns="http://www.w3.org/2000/svg" viewBox={small ? "0 -150 500 500" : "0 0 440 190"} width={small ? 110 : style.width}>
            <path
                style={pathStyle}
                d="M 50,50 L 130,50"
                id={"nandInA"+id+"InA"}
                // onMouseOver={(evt)=>handleMouseOver(evt)}
                // onMouseOut={(evt)=>handleMouseOut(evt)}
            />
            <path
                style={pathStyle}
                d="M 50,130 L 130,130"
                id={"nand"+id+"InB"}
                // onClick={(evt)=>handlePathClick(evt)}
                // onMouseOver={(evt)=>handleMouseOver(evt)}
                // onMouseOut={(evt)=>handleMouseOut(evt)}
            />
            <line 
                x1="320" y1="90" x2="400" y2="90" strokeWidth="10" stroke={hovering ? pathStyle.fill : 'black'} 
                onMouseOver={()=>setHovering(true)}
                onMouseOut={()=>setHovering(false)}
            />
			<circle cx='400' cy='90' r="10" stroke={hovering ? pathStyle.stroke : 'transparent'} strokeWidth="3" fill={hovering ? pathStyle.fill : 'transparent'}
                id={id+"Out"} className={id+"Out"} 
                onMouseOver={()=>setHovering(true)}
                onMouseOut={()=>setHovering(false)}
                onMouseDown={evt=>onMouseDown(evt)}
                onMouseUp={evt=>onMouseUp(evt)}
            />
            <path
                style={pathStyle}
                d="M 207,171 L 135.7,170.99994 L 135.7,12.499996 L 207.46,12.49997 C 246.3576,12.499945 278.3,48.003958 278.3,91.749974 C 278.3,135.49599 246.3576,171 207,171 z"
                onMouseOver={()=>setDraggable(true)}
                onMouseOut={()=>setDraggable(false)}
                onDoubleClick={(evt)=>onDoubleClick(evt)}
                id='nandBody'
            />        
            <path
                style={pathStyle}
                transform="translate(12.90989,-0.353527)"
                d="M 305.47014 89.843887 A 20.152544 20.152544 0 1 1  265.16505,89.843887 A 20.152544 20.152544 0 1 1  305.47014 89.843887 z"
            />
        </svg>
    )

    var img = new Image();
	const svgString = renderToStaticMarkup(<SVG svgStyle={styleAndPosition} small={true} />)
	let blob = new Blob([svgString], {type: 'image/svg+xml'});
	let url = URL.createObjectURL(blob);
	img.src = url;
	img.addEventListener('load', () => URL.revokeObjectURL(url), {once: true});
    // console.log('img = ', img)
    
    function handleDragStart(evt) {
		console.log('in Nand handleDrag evt.target.id = ', evt.target.id)
		evt.dataTransfer.setData('text/plain', evt.target.id)
        evt.dataTransfer.setDragImage(img, img.width/2, img.height/2)
    }

    
    if (divWrap)
        return (
            <div id={id} draggable={draggable} onDragStart={(evt)=>handleDragStart(evt)} style={styleAndPosition}>
                <SVG />
            </div>
        )
    else
        return (
            <SVG svgStyle={style} />
        )
}

export default Nand