import React, { useState } from 'react'

import ComponentsDrawer from './ComponentsDrawer'

import theme from '../../theme' 

import Paper from '@material-ui/core/Paper'

import Nand from '../../Gates/Nand'

import LineTo from 'react-lineto'

import Gates from './Gates'
import Connectors from './Connectors'


const Build = ({ history }) => {

	const [shift, setShift] = useState(0)
	const [dragAreaVisible, setDragAreaVisible] = useState(false)
	const [gates, setGates] = useState({})
	const [connectors, setConnectors] = useState({})
	const [floatingTerminal, setFloatingTerminal] = useState(false)

	const gateTypes = {
		'Nand': Nand
	}

	// eslint-disable-next-line
	String.prototype.hashCode = function() {
		var hash = 0, i, chr;
		if (this.length === 0) return hash;
		for (i = 0; i < this.length; i++) {
		  chr   = this.charCodeAt(i);
		  hash  = ((hash << 5) - hash) + chr;
		  hash |= 0; // Convert to 32bit integer
		}
		return hash;
	  };

	// Add new gate 
	const addGate = (type, position) => {

		const gateType = Object.keys(gateTypes).find(key => gateTypes[key] === type)	// string version of gate type

		// Generate unique gate id
		let id = `${gateType}${JSON.stringify(position)}`	// create estring with gate function definition and position
		id = id.hashCode()									// generate hash 						
		id = [gateType] + id								// prefix with gate type for console clarity
		id = id.toString()									// convert to string to use as key

		// Prepare new gate info
		const newGate = {
			[id]: {							// use id as object key for quick lookup
				type: type,					// record gate type (eg. Nand, And, Or, etc.)
				position: position,			// record initial gate position
				rotation: 'rotate(0deg)',	// gate initially not rotated
			}
		}
		console.log('Build > addGate() : newGate = ', newGate)

		setGates({ ...gates, ...newGate })	// add to existing gates collection
	}


	// Move existing gate to new position
	const moveGate = (id, position) => {

		console.log('Build > moveGate() : gates = ', gates)
		console.log('Build > moveGate() : id = ', id)


		const movedGate = {
			[id]: {
				type: gates[id].type,
				rotation: gates[id].rotation,	
				position: position,
			}
		}

		console.log('Build > moveGate() : movedGate = ', movedGate)

		setGates({ ...gates, ...movedGate })
	}

	const rotateGate = (id, rotation) => {

		console.log('Build > rotateGate() : gates = ', gates)

		setGates({
			...gates,
			[id]: {
				type: gates[id].type,
				rotation: rotation,	
				position: gates[id].position,
			}
		})
	}

	console.log('Build : gates = ', gates)


	let percent = 0.44
	let factor = 0.95
	let gateHeight = 900*0.13
	let gateWidth = 1600*0.13

    const styles = {
        header: {
			backgroundColor: theme.palette.primary.main,
			// minHeight: '90vh',
			display: 'flex',
			flexDirection: 'column',
			alignItems: 'center',
			justifyContent: 'top',
			color: theme.palette.primary.contrastText,
			marginLeft: shift,
		},
		headerTitle: {
			fontSize: 'calc(14px + 2vmin)',
			textShadow: '-1px 0 black, 0 1px black, 1px 0 black, 0 -1px black',
			position: 'relative',
			top: '-35px',
		},
		headerSubtitle: {
			fontSize: 'calc(6px + 2vmin)',
			position: 'relative',
			top: '-55px',
		},
		headerButton: {
			color: theme.palette.primary.light,
		},
		logo: {
			height: gateHeight,
			width: gateWidth,
			pointerEvents: 'auto',
			// cursor: 'grab',
			fill: theme.palette.primary.light,
			position: 'absolute',
			zIndex: 99998,
		},
		connector: {
			// fill: theme.palette.primary.light,
			// position: 'relative',
			// top: -2.5,
			// zIndex: 99999,
			// transform: 'translateY(-3px)'
			pointerEvents: 'none',
		},
		paper: {
			top: -30, 
			width: 1600*percent, 
			height: 900*percent, 
			position: 'relative', 
			zIndex: 9999, 
			display: 'flex', 
			alignItems: 'center', 
			justifyContent: 'center'
		},
		div: { 
			color: theme.palette.primary.light, 
			borderStyle: dragAreaVisible ? 'dashed' : 'hidden', 
			borderRadius: 3, 
			borderWidth: 1, 
			borderColor: theme.palette.primary.light, 
			width: 1600*percent*factor, 
			height: 900*percent*factor, 
			display: 'flex', 
			justifyContent: 'center' 
		}
    }	
	

	function handleDragOver(evt) {
		evt.preventDefault()
		setDragAreaVisible(true)
	}

	function handleDragDrop(evt, gates) {
		evt.preventDefault()

		let id = evt.dataTransfer.getData('text')

		console.log('Build > handleDragDrop() : id = ', id)

		let paper = document.getElementById('paper').getBoundingClientRect();

		let position = { 
			left: evt.clientX-gateWidth/2-paper.left, 
			top: evt.clientY-gateHeight/2-paper.top
		}

		console.log('Build > handleDragDrop : gates = ', gates)


		if (id.includes('new')) 
			addGate(gateTypes[id.replace('new', '')], position)
		else if (id.includes('Nand'))
			moveGate(id, position)


		setDragAreaVisible(false)

		// const newConnectors = connectors
		// setConnectors(newConnectors)
	}



	function handleDoubleClick(evt, gateId) {

		console.log('Build > handleDoubleClick() : gateId = ', gateId)

		let currentGateRotation = gates[gateId].rotation
		let newGateRotation

        if 		(currentGateRotation === 'rotate(0deg)') 							newGateRotation = 'translate(-10%, -5%) rotate(90deg)'
        else if (currentGateRotation === 'translate(-10%, -5%) rotate(90deg)') 		newGateRotation = 'translate(-5%, -25%) rotate(180deg)'
        else if (currentGateRotation === 'translate(-5%, -25%) rotate(180deg)') 	newGateRotation = 'translate(5%, -15%) rotate(270deg)'
		else if (currentGateRotation === 'translate(5%, -15%) rotate(270deg)') 		newGateRotation = 'rotate(0deg)'

		console.log('Build > handleDoubleClick: newGateRotation = ', newGateRotation)
		
		rotateGate(gateId, newGateRotation)
	}



	function handleMouseDownTerminal(evt) {
		console.log('Build > handleMouseDownTerminal() : evt = ', evt)
		console.log('Build > handleMouseDownTerminal() : x and y = ', evt.clientX, ' ', evt.clientY)

		const fromTerminal = document.getElementById(evt.target.id)
		console.log('Build > handleMouseDownTerminal() : fromTerminal = ', fromTerminal)

		const fromTerminalId = fromTerminal.id
		console.log('Build > handleMouseDownTerminal() : fromTerminalId = ', fromTerminalId)

		const toTerminalId = 'floatingTerminal'
		console.log('Build > handleMouseDownTerminal() : toTerminalId = ', toTerminalId)

		const connectorId = fromTerminalId + '_' + toTerminalId
		console.log('Build > handleMouseDownTerminal() : connectorId = ', connectorId)

		const paper = document.getElementById('paper').getBoundingClientRect()
		// console.log('Build > handleMouseDownTerminal(): paper = ', paper)

		const newConnector = {
			[connectorId]: {
				type: LineTo,
				from: fromTerminalId,	
				to: toTerminalId
			}
		}
		
		setConnectors({	...connectors, ...newConnector	})

		setFloatingTerminal({ 
			x: evt.clientX - paper.left - 4 + 'px', // 4 is half of divPos width and height
			y: evt.clientY - paper.top - 4 + 'px'
		})
	}

	function handleMouseUpTerminal(evt) {
		console.log('Build > handleMouseUpTerminal() : evt = ', evt)
		console.log('Build > handleMouseUpTerminal() : x and y = ', evt.clientX, ' ', evt.clientY)

		const toTerminal = document.getElementById(evt.target.id)
		console.log('Build > handleMouseUpTerminal() : toTerminal = ', toTerminal)

		const toTerminalId = toTerminal.id
		console.log('Build > handleMouseUpTerminal() : toTerminalId = ', toTerminalId)


		let newConnectors = connectors
		let newConnector
		for (let connectorId in newConnectors)
		{
			console.log('Build > handleMouseUp() > for loop : connectorId = ', connectorId)

			if (connectorId.includes('floatingTerminal'))
			{
				console.log('Build > handleMouseUp() > for loop : replacing connectorId = ', connectorId)
				console.log('Build > handleMouseUp() > for loop : replacing newConnectors[connectorId] = ', newConnectors[connectorId])
				
				const fromTerminalId = connectorId.replace('_floatingTerminal', '')
				const newConnectorId = connectorId.replace('floatingTerminal', toTerminalId)
				console.log('Build > handleMouseUpTerminal() : newConnectorId = ', newConnectorId)

				delete newConnectors[connectorId]
				newConnector = {
					[newConnectorId]: {
						type: LineTo,
						from: fromTerminalId,	
						to: toTerminalId
					}
				}
			}
		}

		console.log('Build > handleMouseUp() : newConnectors = ', newConnectors)

		// const connectorId = fromTerminalId + '_' + toTerminalId
		// console.log('Build > handleMouseUpTerminal() : connectorId = ', connectorId)

		const paper = document.getElementById('paper').getBoundingClientRect()
		// console.log('Build > handleMouseDownTerminal(): paper = ', paper)


		
		setConnectors({	...newConnectors, ...newConnector	})

		setFloatingTerminal(false)
	}


	function handleMouseMove(evt) {
		

		const paper = document.getElementById('paper').getBoundingClientRect()
		// console.log('Build > handleMouseMove(): paper = ', paper)

		const floatingTerminalSvgPosition = {
			x: evt.clientX - paper.left - 4 + 'px', // 4 is half of divPos width and height
			y: evt.clientY - paper.top - 4 + 'px'
		}

		console.log('Build > handleMouseMove(): floatingTerminalSvgPosition x and y = ', floatingTerminalSvgPosition.x, ' ', floatingTerminalSvgPosition.y)

		floatingTerminal && setFloatingTerminal({ x: floatingTerminalSvgPosition.x, y: floatingTerminalSvgPosition.y })

		let newConnectors = {
			...connectors,
			update: true,
		}
		
		setConnectors(newConnectors)
	}

	function handleMouseUp(evt) {
		console.log('Build > handleMouseUp() : evt = ', evt)

		setFloatingTerminal(false)

		let newConnectors = connectors
		for (let connectorId in newConnectors)
		{
			console.log('Build > handleMouseUp() > for loop : connectorId = ', connectorId)

			if (connectorId.includes('floatingTerminal'))
			{
				console.log('Build > handleMouseUp() > for loop : removing connectorId = ', connectorId)
				console.log('Build > handleMouseUp() > for loop : removing newConnectors[connectorId] = ', newConnectors[connectorId])
				delete newConnectors[connectorId]
			}
		}

		console.log('Build > handleMouseUp() : newConnectors = ', newConnectors)

		setConnectors({ ...newConnectors })
	}

	console.log('Build : connectors = ', connectors)
	console.log('Build : floatingTerminal = ', floatingTerminal)


    return (
        <div style={styles.header} onDragOver={(evt) => handleDragOver(evt)} onDrop={(evt) => setDragAreaVisible(false)}>
            <p style={styles.headerTitle}>
                Build 
            </p>
            <p style={styles.headerSubtitle}>
                Drag components into the observer to view their details or drag them onto the canvas to use them 
            </p>
			<ComponentsDrawer setShift={setShift} />
			<Paper 
				id='paper' 
				style={styles.paper}>
				<div 
					id='dropZone' 
					style={styles.div} 
					onDrop={evt=>handleDragDrop(evt, gates)} 
					onMouseMove={evt=>handleMouseMove(evt)}
				>
					{/* {dragAreaVisible ? 'Drop here to use' : ''} */}
					<Gates 
						gates={gates} 
						onDoubleClick={handleDoubleClick} 
						onMouseDown={handleMouseDownTerminal} 
						onMouseUp={handleMouseUpTerminal}
						style={styles.logo} 
						setConnectors={setConnectors}
						connectors={connectors}
					/>
					<Connectors 
						connectors={connectors} 
						floatingTerminal={floatingTerminal} 
						// onMouseUp={handleMouseUp} 
						style={styles.connector} 
					/>
				</div>
			</Paper>
        </div>
    )
}



export default Build