import React from 'react'

import theme from '../../theme' 


const Connectors = ({ connectors, floatingTerminal, onMouseUp, style }) => {
		
    console.log('Build > Connectors : connectors = ', connectors)

    const FloatingTerminal = () => (
        <svg key='floatingTerminalsvg' width='8' height='8' viewBox='0 0 8 8' style={{ overflow: 'visible', position: 'absolute', left: floatingTerminal.x, top: floatingTerminal.y, zIndex: 999999999, pointerEvents: 'none' }} onMouseUp={evt=>onMouseUp(evt)}>
            <circle id={'floatingTerminal'} className='floatingTerminal' cx='50%' cy='50%' r="4" stroke={theme.palette.primary.light} strokeWidth="3" fill={theme.palette.primary.light} />
        </svg>
    )

    console.log('Build > Connectors : floatingTerminal = ', floatingTerminal)

    let connectorsArray = floatingTerminal ? [<FloatingTerminal key='floatingTerminal' />] : []
    for (let connectorId in connectors)
    {
        if (connectorId === 'update')
            continue

        console.log('Build > Connectors : connectorId = ', connectorId)

        let currentConnector = connectors[connectorId]
        console.log('Build > Connectors : currentConnector = ', currentConnector)

        connectorsArray.push(
            <currentConnector.type 
                key={connectorId} 
                id={connectorId} 
                style={style} 
                from={currentConnector.from} 
                to={currentConnector.to} 
                zIndex={999999999} 
                fromAnchor='50% 37.12%' 
                toAnchor='50% 25%' 
                borderColor={theme.palette.primary.light} 
                borderWidth={5} 
            /> 
        )
    }

    console.log('Build > Connectors : connectorsArray = ', connectorsArray)
    if (connectorsArray.length === 0) 
        return null
    else
        return connectorsArray
}

export default Connectors